<template>
  <v-sheet tile>
    <v-sheet
      class="mt-6"
      tile
      v-if="!this.isLoading"
    >
      <header-title class="mt-7 mb-2">
        {{ DAMAGED_TITLE }}
      </header-title>
      <header-small-title class="mb-4">
        {{ DAMAGED_SUBTITLE }}
      </header-small-title>
      <div
        :item="item"
        :class="{
          'd-flex align-center justify-center': this.$vuetify.breakpoint
            .smAndDown,
          'd-flex align-start justify-start': this.$vuetify.breakpoint.mdAndUp,
        }"
      >
        <product-card
          :height="192"
          v-bind="item"
          :src="this.item.image_url"
          :width="192"
          basic
        />
      </div>
      <v-col>
        <action-button
          @click.stop="onClickMethod"
          primary-color
          :loading="isLoading || isMapLoading"
          :block="$vuetify.breakpoint.smAndDown"
          large
        >
          {{ DAMAGED_SUBMIT_ACTION }}
        </action-button>
      </v-col>
    </v-sheet>
    <div v-else>
      <v-skeleton-loader type="card" />
    </div>
  </v-sheet>
</template>

<script>
import { mapGetters } from 'vuex'
import { EXCHANGE_REFUND } from 'branding/strings.json'
import HeaderSmallTitle from '@/components/molecules/HeaderSmallTitle.vue'
import HeaderTitle from '@/components/molecules/HeaderTitle.vue'
import { saveSelectedItemToStore } from '@/controllers/ExchangesController'

export default {
  name: 'ExchangeDamagedView',
  components: {
    ActionButton: () => import('@/components/atoms/ActionButton.vue'),
    ProductCard: () => import('@/components/organism/ProductCard.vue'),
    HeaderSmallTitle,
    HeaderTitle
  },
  data: () => ({
    DAMAGED_TITLE: EXCHANGE_REFUND.DAMAGED_TITLE,
    DAMAGED_SUBTITLE: EXCHANGE_REFUND.DAMAGED_SUBTITLE,
    DAMAGED_SUBMIT_ACTION: EXCHANGE_REFUND.DAMAGED_SUBMIT_ACTION
  }),
  props: {
    returnReasonClicked: {
      type: Function,
      required: true
    },
    isMapLoading: {
      type: Boolean,
      required: true
    }
  },
  computed: {
    ...mapGetters({
      currentItemIndex: 'returns/currentItemIndex',
      selectedItems: 'order/selectedItems',
      isLoading: 'exchange/isLoading'
    }),
    item () {
      return this.selectedItems[this.currentItemIndex]
    }
  },
  methods: {
    onClickMethod (event) {
      saveSelectedItemToStore(this.item)
      this.returnReasonClicked()
      this.$emit('click', event)
    }
  },
  mounted () {}
}
</script>
